import { createStore } from 'vuex'

export default createStore({
  state: {
    rawData:[],
    num:'',
    types:[],
    process:0
  },
  getters: {
    getData(state) {
      return state.rawData
    },
    getNum(state) {
      return state.num
    },
    getTypes(state) {
      return state.types
    },
    getProcess(state) {
      return state.process
    }
  },
  mutations: {
    setRawData(state,data) {
      return state.rawData=data;
    },
    setNum(state,data) {
      return state.num=data;
    },
    setTypes(state,data) {
      return state.types=data;
    },
    setProcess(state,data) {
      return state.process=data;
    }
  },
  actions: {
  },
  modules: {
  }
})
