<template>
    <span class="processParam"> 每组人数：</span>
    <el-select class="processParam" v-model="value" placeholder="小组人数" style="margin-right: 10px">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  <el-button @click=start class="processParam" type="success">开始分组</el-button>
  <span class="processParam"> 一共读取到 {{getNum}} 条数据</span>
    <el-button @click=download type="success" class="processParam"> 下载分组结果 </el-button>
    <div style="float:right;width:100%">
        <el-main id="result">
    <div style="margin-bottom:10px">
      {{Note}}
    </div>
  <el-tabs v-model="editableTabsValue" type="card">
  <el-tab-pane
    :key="item.groupId"
    v-for="(item) in afterData"
    :label="item.group"
    :name="item.group"
  >
    <el-table
    id="detailList"
    :data="item.people"
    height="550px">
    <el-table-column v-for="(item,index) in types" :key=index :prop=item.id :label=item.name width="180"></el-table-column>
    </el-table>
  </el-tab-pane>
    </el-tabs>
  </el-main>
        <div style="margin-top:10px;margin-bottom:10px;font-family: Avenir, Helvetica, Arial, sans-serif;-webkit-font-smoothing: antialiased;-moz-osx-font-smoothing: grayscale;">
    <span><a href="https://www.zhihu.com/question/426826559">当代大学生是如何被内卷的？</a> - 知乎
</span>
  </div>
    </div>
    <img src="../assets/jack.jpg" height="220">
</template>

<script>
import FileSaver from 'file-saver'
export default {
    data() {
        return {
          options: [{
            value: '2',
            label: '2'
          }, {
            value: '3',
            label: '3'
          }, {
            value: '4',
            label: '4'
          }, {
            value: '5',
            label: '5'
          }, {
            value: '6',
            label: '6'
          }, {
            value: '7',
            label: '7'
          }, {
            value: '8',
            label: '8'
          }],
          value:'',
          rawData:[],
          types:[],
        afterData: [
        ],
        }
    },
    methods: {
        download() {
            const XLSX = require('xlsx')
            var wopts = {
                bookType: 'xlsx',
                bookSST: true,
                type: 'binary'
            };
            const sheets=[]
            for(let i = 0;i<this.afterData.length;i++) {
                sheets.push('第'+(i+1)+'组')
            }
            var workBook = {
                SheetNames: sheets,
                Sheets: {},
                Props: {}
            };
            console.log(this.afterData[0])
            for(let i =0;i<this.afterData.length;i++) {
                workBook.Sheets['第'+(i+1)+'组']=XLSX.utils.json_to_sheet(this.afterData[i].people);
            }
            FileSaver.saveAs(new Blob([this.changeData(XLSX.write(workBook, wopts))], {type: 'application/octet-stream'}), "分组结果.xlsx")
        },
        changeData(s) {

    //如果存在ArrayBuffer对象(es6) 最好采用该对象
    if (typeof ArrayBuffer !== 'undefined') {

      //1、创建一个字节长度为s.length的内存区域
      var buf = new ArrayBuffer(s.length);

      //2、创建一个指向buf的Unit8视图，开始于字节0，直到缓冲区的末尾
      var view = new Uint8Array(buf);

      //3、返回指定位置的字符的Unicode编码
      for (var i = 0; i < s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
      return buf;

    } else {
      var buf = new Array(s.length);
      for (var i = 0; i < s.length; ++i) buf[i] = s.charCodeAt(i) & 0xFF;
      return buf;
    }
  },
      start() {
        if(this.value=='') {
            this.$message({
              type: 'warning',
              message: '请选择人数！'
            })
        } else {
            this.types=this.$store.getters.getTypes
            this.afterData=[]
            const num=this.$store.getters.getNum
            const after=[]
            const raw = this.$store.getters.getData
            for(let i=0;i<num;i++) {
                const obj={}
                obj.id=i
                obj.weight=Math.random()
                after.push(obj)
            }
            after.sort(this.sortByNum)
            let groupNum = Math.ceil(num/this.value)
            for(let i = 0; i< groupNum-1;i++) {
                const object = {}
                object.groupId=i;
                object.group="第"+(i+1)+"组"
                var people = []
                for(let j = 0;j<this.value;j++) {
                    people.push(raw[after[this.value*i+j].id])
                }
                object.people = people
                this.afterData.push(object)
            }
            const object = {}
            object.groupId=groupNum-1;
            object.group="第"+(groupNum)+"组"
            var people = []
            for(let i = (groupNum-1)*this.value;i<num;i++) {
                people.push(raw[after[i].id])
            }
            object.people = people
            this.afterData.push(object)
            this.$store.commit('setProcess',2);
            console.log(this.afterData)
        
        }
      },
      sortByNum(a,b) {
          return a.weight-b.weight
      }
    },
    computed: {
      getNum() {
        return this.$store.getters.getNum
      },
      Note() {
        switch(this.$store.getters.getProcess) {
          case 0:
            return "请先导入数据"
          case 1:
            return "请开始分组"
          default:
            return ''
        }
      }
    }
}
</script>
<style scoped>
.processParam {
    margin-left: 20px;
    margin-bottom: 10px
}
#result {
    margin-top: 10px;
    border:5px solid rgb(163, 135, 135);
    margin-right: 5%;
    height: 700px;
}
</style>